import * as React from "react";
import { withLayout } from "../components/Layout";
import { Grid, Image } from 'semantic-ui-react'
import { StaticQuery, graphql } from "gatsby"

const MoviesPage = (props) => {

    document.body.style.backgroundColor = "#1D1D2C"

    const movie = props.data.movies.movie

    return (
        <div>
            <Grid style={{ marginTop: 50 }}>
                <Grid.Column textAlign="center" width={4} verticalAlign='bottom' >
                    <Image centered style={{ height: 300, width: 220, borderRadius: 5 }} src={`https://image.tmdb.org/t/p/w500/${movie.posterPath}`} />
                </Grid.Column>
                <Grid.Column width={9}>
                    <h1 style={{color: "#454569", fontWeight: 'bold'}}>{movie.title}</h1>
                    <p style={{color: "#BEBEC2"}}>{movie.releaseDate}</p>                    
                    <p style={{color: "#BEBEC2"}}>Rating: {movie.voteAverage}</p>
                    <p style={{color: "#BEBEC2"}}>{movie.overview}</p>                    
                </Grid.Column>
            </Grid>
        </div>
    );
};
export default withLayout(MoviesPage);

export const Query = graphql`
query MoviesQuery($movieId: ID!){
    movies {
        movie(id: $movieId ){          
            title
            releaseDate
            posterPath
            id            
            voteAverage     
            overview
        }  
    }
}`;